/*------------------------------------*		#BASE
\*------------------------------------*/
/**
 * 1. Make every element inherit the value for the box-sizing property. This 
 * 		allows for easy overriding and cascading.
 */
*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  font-size: inherit; }

/**
 * 1. Auto-hiding scrollbars are used if the element's content overflows
 *		(IE 10/11).
 * 2. Disable unwanted auto-enlargement of small text in mobile browsers.
 * 3. Set to border-box so the width/height of an element is calculated to
 *		include padding.
 * 4. Set root font-size to 0.625rem that 1rem == 10px.
 */
:root {
  -ms-overflow-style: -ms-autohiding-scrollbar;
  text-size-adjust: 100%;
  box-sizing: border-box;
  font-size: 0.625rem;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1.618;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  cursor: default; }

/**
 * 1. Hide auto element in Chrome 40+, iOS 8+, Safari 8+.
 */
audio:not([controls]) {
  display: none; }

details {
  display: block; }

input[type="number"] {
  width: auto; }

input[type="search"] {
  -webkit-appearance: textfield; }
  input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none; }

main {
  display: block; }

pre {
  overflow: auto; }

textarea {
  overflow: auto; }

[hidden] {
  display: none; }

[unselectable] {
  user-select: none; }

a {
  text-decoration: none; }

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle; }

button,
input,
select,
textarea {
  color: inherit;
  font-family: inherit;
  font-style: inherit;
  font-weight: inherit; }

code,
kbd,
pre,
samp {
  font-family: monospace; }

